<template>
    <div>
       <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  el-table border -->
    <el-card>
        <el-table :data="rightsList" border stripe>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="authName" label="" align="center"></el-table-column>
            <el-table-column prop="path" label=""></el-table-column>
            <el-table-column prop="level" label="">
                <!-- scope  elemnt -->
                <template v-slot:default="scope">
                    <el-tag v-if="scope.row.level==='0'"></el-tag>
                    <el-tag type="success" v-else-if="scope.row.level==='1'"></el-tag>
                    <el-tag type="warning" v-else-if="scope.row.level==='2'"></el-tag>
                </template>
            </el-table-column>
        </el-table>
    </el-card>

    </div>
</template>

<script>
export default {
    data(){
        return {
            //
            rightsList:[]
        }
    },
    created(){
        //
        this.getRightsList()
    },
    methods:{
        //
        async getRightsList(){
            const {data:result}=await this.$http.get('rights/list')
            if(result.meta.status!==200) return this.$message.error('')
            this.rightsList = result.data
            //console.log(result)
        }
    }

}
</script>

