<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!--   :modelv-bind:model refloginFormRef-->
      <el-form ref="loginFormRef" :model="loginForm" label-width="0px" class="login_form" :rules="loginFormRules">
        <!--  -->
        <el-form-item prop="username">
          <!-- v-model -->
          <el-input v-model="loginForm.username" prefix-icon="el-icon-user-solid"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      loginForm: {
        username: "admin",
        password: "123456"
      },

      //
      loginFormRules: {
        //
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 10, message: "3", trigger: "blur" }
        ],
        //
        password: [
            { required: true, message: "", trigger: "blur" },
            { min: 6, max: 15, message: "615", trigger: "blur" }
        ]
      }
    };
  },

  methods:{
      //
      resetLoginForm(){
          //console.log(this) $refs loginFormRef  resetFields() 
          //refs
          this.$refs.loginFormRef.resetFields();
      },
      login(){
          //Lambda  
          this.$refs.loginFormRef.validate(async (valid)=>{
              //console.log(valid);//true false 
              if(!valid) return ;
              //axios
              // 
              //const {data:result}=await this.$http.post('login',this.loginForm);
              const result=await this.$http.post('login',this.loginForm);
              //this.$message elemnt ui 
              if(result.data.meta.status!==200) return this.$message.error('');
              this.$message.success('');
              //console.log(result.data.data.token);
              window.sessionStorage.setItem("token",result.data.data.token);
              this.$router.push("/home");
          });
      }
  }
};
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>