<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
      <!--  :space="200"  +  - /  % nuber  -0 number :active="parseInt(activeIndex)"-->
      <el-steps
        :active="activeIndex - 0"
        finish-status="success"
        align-center
        process-status="process"
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tab -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          tab-position="left"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <!-- type="number" -->
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item labei="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name' ,children:'children' }"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- Item -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb,index) in item.attr_vals" :key="index" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <!-- <el-input v-for="(cb, i) in item.attr_vals" :key="i" v-model="item.attr_vals[i]"> -->
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!--actionAPI   uploadURL : this.$http.defaults.baseURL + "/upload"-->
            <!--on-preview   -->
            <!-- on-removeX  -->
            <!-- list-type    -->
            <!-- on-success -->
            <el-upload
              action="http://www.ysqorz.top:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce">
            </quill-editor>
            <!--  -->
            <el-button type="primary" class="add_goods" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog
  title=""
  :visible.sync="previewVisible"
  width="50%"
  >
  <img :src="previewPath" alt="" class="previewImg"> 
  <!-- npm install vue-quill-editor --save -->
  
</el-dialog>


  </div>
</template>

<script>
export default {
  data() {
    return {
      // js 
      activeIndex: 0,
      //
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        //
        goods_cat: [],
        //
        pics:[],
        // 
        goods_introduce:'',
        attrs:[]
      },
      //
      addFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      cateList: [],
      //
      manyTableData: [],
      //
      onlyTableData: [],
      //headers
      //token,
      headerObj:{
        Authorization:window.sessionStorage.getItem('token')
      },
      // 
      previewPath:'',
      previewVisible:false
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    //
    async getCateList() {
      const { data: result } = await this.$http.get("categories");
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.cateList = result.data;
      console.log(this.cateList);
    },
    //
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) this.addForm.goods_cat = [];
    },
    // 
    beforeTabLeave(activeName, oldActiveName) {
      // console.log(`:${oldActiveName}`)
      // console.log(`:${activeName}`)
      // return false
      if (oldActiveName === "0" && this.addForm.goods_cat.length !== 3) {
        this.$message.error("");
        return false;
      }
    },
    //resultifresultundefine
    async tabClicked() {
      if (this.activeIndex === "1") {
        const { data: result } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: "many" }
          }
        );
        if (result.meta.status !== 200) {
          return this.$message.error("");
        }
        result.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(" ");
        });
        this.manyTableData = result.data;
        //console.log(this.manyTableData)
      } else if (this.activeIndex === "2") {
        const { data: result } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: "only" }
          }
        );
        if (result.meta.status !== 200) {
          return this.$message.error("");
        }
        this.onlyTableData = result.data;
      }
    },
    //
    handlePreview(file){
      // console.log(file)
      // this.previewPath=file.response.data.url.slice(22)
      // console.log(this.previewPath)
      this.previewPath = this.$http.defaults.baseURL + file.response.data.tmp_path
      console.log(this.previewPath)
      this.previewVisible = true 
    },
    //
    handleRemove(file){
      //console.log(file)
      //1
      const filePath=file.response.data.tmp_path
      //2pics,
      const i=this.addForm.pics.findIndex(x => x.pic === filePath)
      //3solice, pics
      this.addForm.pics.splice(i,1)
      console.log(this.addForm)
    },
    //
    handleSuccess(response){
      console.log(response)
      //1 
      const picInfo={pic:response.data.tmp_path}
      //2 pushpics
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    add(){
      //console.log(this.addForm)
      this.$refs.addFormRef.validate(async valid => {
        if(!valid){return this.$message.error('')}
        //
        //this.addForm.goods_cat=this.addForm.goods_cat.join(',')  el-cascaderv-model  
        // npm install lodash  cloneDeep(obj) 
        var form = JSON.parse(JSON.stringify(this.addForm))
        //
        this.manyTableData.forEach(item => {
          const newInfo={attr_id:item.attr_id,attr_value:item.attr_vals.join(' ')}
          this.addForm.attrs.push(newInfo)
        })
        //
        this.onlyTableData.forEach(item=>{
          const newInfo={attr_id:item.attr_id,attr_value:item.attr_vals}
          this.addForm.attrs.push(newInfo) 
        })
        form.attrs=this.addForm.attrs
        let dd=form.goods_cat.toString()
        form.goods_cat=dd
        console.log(form)

        //   attr_value: item.attr_vals === [] ? "" : item.attr_vals.join(" ")
        const {data:result} = await this.$http.post('goods',form)
        console.log(result)
        if(result.meta.status!==201){
          return this.$message.error('')
        }
        this.$message.success('')
        // 
        this.$router.push('/goods')
      })

    }
  },
  //
  computed: {
    cateId() {
      //return this.addForm.goods_cat.length === 3 ? this.addForm.goods_cat[2] : null
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2];
      }
      return null;
    }
  }
};
</script>

<style lang="less" scoped>
.el-steps {
  margin: 15px 0;
}
.el-checkbox {
  margin: 0 5px 0 0 !important;
}
.previewImg{
  width: 100%
}
.add_goods{
  margin-top: 10px;
}
</style>