<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  el-table-colum  type="expand"  el-row el-col-->
        <el-table-column type="expand">
          <template v-slot:default="scope">
            <!-- ['bdbottom']   i1 index-->
            <el-row
              :class="['bdbottom',i1===0? 'bdtop' : '','vcenter']"
              v-for="(item1,i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  span 5 19 5+19=24 row24-->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!--for   -->
                <el-row
                  :class="[index2=== 0 ? '' : 'bdtop','vcenter']"
                  v-for="(item2,index2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!--  -->
                    <el-tag
                      type="warning"
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label="" align="center"></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot:default="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeUserById(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  @close-->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%">
      <!--   prop-->
      <el-form :model="addForm" ref="addFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  ref id -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editForm" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="SetRightDialogVisible" width="50%" @close="setRightDialogClosed">
      <!--   node-key="id" ID  -->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="SetRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      rolelist: [],
      //
      addForm: {
        roleName: "",
        roleDesc: ""
      },
      //
      addDialogVisible: false,
      //
      editDialogVisible: false,
      editForm: {},
      //
      SetRightDialogVisible:false,
      //
      rightsList:[],
      //
      treeProps:{
        label:'authName',
        children:'children'
      },
      //ID
      defKeys:[],
      //ID
      roleId:''
    };
  },

  created() {
    this.getRolelist();
  },

  methods: {
    //
    async getRolelist() {
      const { data: result } = await this.$http.get("roles");
      if (result.meta.status !== 200)
        return this.$message.error("");
      this.rolelist = result.data;
      //console.log(this.rolelist)
    },

    //
    async addUser() {
      const { data: result } = await this.$http.post("roles", this.addForm);
      if (result.meta.status !== 201) {
        return this.$message.error("");
      }
      this.$message.success("");
      //
      this.addDialogVisible = false;
      //
      this.getRolelist();
    },
    //
    async showEditDialog(id) {
      const { data: result } = await this.$http.get("roles/" + id);
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.editForm = result.data;
      this.editDialogVisible = true;
    },

    //
    async removeUserById(id) {
      //
      const confirmresult = await this.$confirm(
        ",?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      // confirm cancel
      if (confirmresult !== "confirm") {
        return this.$message.info("");
      }
      const { data: result } = await this.$http.delete("roles/" + id);
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getRolelist();
    },

    //
    async editUserInfo() {
      const { data: result } = await this.$http.put(
        "roles/" + this.editForm.roleId,
        { roleName: this.editForm.roleName, roleDesc: this.editForm.roleDesc }
      );
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.editDialogVisible = false;
      this.$message.success("");
      this.getRolelist();
    },

    //ID
    async removeRightById(role, rightId) {
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: result } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      );
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      //this.getRolelist()    
      // role roletable vue 
      //vuescopev-bind
      //
      role.children = result.data;
    },

    //
    async showSetRightDialog(role) {
      this.roleId=role.id
      //
      const {data:result}=await this.$http.get('rights/tree')
      if(result.meta.status!==200){
        return this.$message.error('');
      }
      //data
      this.rightsList=result.data 
      //console.log(this.rightsList)
      //this.defKeys=[]  setRightDialogClosed()
      //ID
      this.getLeafKeys(role,this.defKeys)
      this.SetRightDialogVisible=true
    },

    // ID defKeys
    getLeafKeys(node,arr){
      //nodechildren,
      if(!node.children){
        return arr.push(node.id)
      }
      node.children.forEach(item=>this.getLeafKeys(item,arr))
    },

    //
    setRightDialogClosed(){
      this.defKeys=[]
    },
    //
    async allotRights(){
      const keys= [
        //...
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedNodes()
      ]
      //console.log(keys) ID
      const idStr=keys.join(',')
      const {data:result}=await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
      if(result.meta.status!==200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.SetRightDialogVisible = false
      this.getRolelist()
    }
  }
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>