<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt />
        <span></span>
      </div>
      <el-button type="info" @click="logout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse?'64px':'200px'">
        <!--  -->
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!--  unique-opened, true unique-opened :unique-opened="true"  -->
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="this.$route.path"
        >
          <!--  :  :index ''  item.id :index -->
          <el-submenu :index="item.id + '' " v-for="item in menulist" :key="item.id">
            <!--  -->
            <template slot="title">
              <!--  -->
              <i :class="iconsObj[item.id]"></i>
              <!--  -->
              <span>{{item.authName}}</span>
            </template>
            <!--  -->
            <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id">
              <!--  -->
              <i class="el-icon-menu"></i>
              <!--  -->
              <span>{{subItem.authName}}</span>
            </el-menu-item>
          </el-submenu>
          
        </el-menu>
      </el-aside>
      <!--  -->
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data(){
    return{
      //
      menulist:[],
      iconsObj:{
        '125':'el-icon-user',
        '103':'el-icon-check',
        '101':'el-icon-goods',
        '102':'el-icon-s-order',
        '145':'el-icon-s-marketing',
      },
      // false
      isCollapse:false
    }
  },
  //
  created(){
    this.getMenuList()
  },
  methods: {
    logout() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    //
    async getMenuList(){
      //const result = await this.$http.get('menus');
      const {data:result} = await this.$http.get('menus');
      if(result.meta.status!==200) return this.$message.console.error(result.meta.msg);
      this.menulist=result.data;
      //console.log(result);
    },
    // 
    toggleCollapse(){
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #ffffff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #333744;
  .el-menu{
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}

.toggle-button{
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  // letter-spacing
  letter-spacing: 0.2em;
  //
  cursor: pointer;
}
</style>