<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--   :gutter20 @-->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>

      <!-- table -->
      <el-table :data="goodsList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <!--propgoodsList    -->
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="()" prop="goods_price" width="95px"></el-table-column>
        <el-table-column label="" prop="goods_weight" width="70px"></el-table-column>
        <el-table-column label="" prop="add_time" width="180px">
          <!-- scope.row.add_time * 1000 | dateFormat   | filter | Vue-->
          <template v-slot:default="scope">{{scope.row.add_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="145px">
          <template v-slot:default="scope">
            <!--  scope.row.idid id -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.goods_id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              @click="removeUserById(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editVisible" width="50%">
      <el-form :model="goodsSerachList" :rules="editFormRules" ref="editFormRef" label-width="80px">
        <el-form-item label="" prop="goods_name">
          <el-input v-model="goodsSerachList.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="goodsSerachList.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="goodsSerachList.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="goodsSerachList.goods_weight"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false"> </el-button>
        <el-button type="primary" @click="putEditGoods(goodsSerachList.goods_id)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      //
      goodsList: [],
      //
      total: 0,
      //
      goodsSerachList: [],
      editVisible: false,
      editFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.getGoodsList();
  },
  methods: {
    // get params
    async getGoodsList() {
      const { data: result } = await this.$http.get("goods", {
        params: this.queryInfo
      });
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      //console.log(result)
      this.goodsList = result.data.goods;
      this.total = result.data.total;
    },

    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getGoodsList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getGoodsList();
    },
    //ID
    async removeUserById(id) {
      const confirmresult = await this.$confirm(
        ",?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      if (confirmresult !== "confirm") {
        return this.$message.info("");
      }
      const { data: result } = await this.$http.delete(`goods/${id}`);

      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getGoodsList();
    },
    // 
    goAddpage() {
      this.$router.push("/goods/add");
    },
    //ID
    async showEditDialog(id) {
      const { data: result } = await this.$http.get(`goods/${id}`);
      console.log(result);
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.goodsSerachList = result.data;
      this.editVisible = true;
    },
    //
    putEditGoods(id) {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return;
        }
        const result = await this.$http.put(`goods/${id}`, {
          goods_name: this.goodsSerachList.goods_name,
          goods_price: this.goodsSerachList.goods_price,
          goods_number: this.goodsSerachList.goods_number,
          goods_weight: this.goodsSerachList.goods_weight
        });
        this.editVisible = false;
        console.log(result);
      });
    }
  }
};
</script>

<style lang="less" scoped>
</style>