<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- -->
    <el-card>
      <!--   :gutter20 @-->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="userList" border stripe>
        <!-- el-table-columntype=index  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="" prop="mg_state">
          <!-- slot-scope  <template v-slot:default="slotProps"> {{scope.row}} -->
          <!-- <template v-slot="slotProps">
             {{slotProps.row.mg_state}}
          </template>-->
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot:default="scope">
            <!--  scope.row.idid id -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              round
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              round
              @click="removeUserById(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" round @click="setRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  @close-->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--   prop-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  ref id -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <!-- disabled -->
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="setRoleDialogVisible" width="30%" @close="setRoleDialogClosed">
      <div>
        <p>:{{userInfo.username}}</p>
        <p>:{{userInfo.role_name}}</p>
        <p>
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    //cbcallback()
    //
    var checkEmail = (rule, value, cb) => {
      //
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;

      if (regEmail.test(value)) {
        //
        return cb();
      }
      //
      cb(new Error(""));
    };

    //
    var checkMobile = (rule, value, cb) => {
      //
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (regMobile.test(value)) {
        return cb();
      }
      cb(new Error(""));
    };

    return {
      //
      queryInfo: {
        query: "",
        //
        pagenum: 1,
        //
        pagesize: 5
      },
      userList: [],
      total: 0,
      //
      addDialogVisible: false,
      //
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      //
      addFormRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3~10",
            trigger: "blur"
          }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 10,
            message: "3~10",
            trigger: "blur"
          }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          //{ type: "email", message: "", trigger: ["blur", "change"]}
          { validator: checkEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          // checkMobile methods this
          { validator: checkMobile, trigger: "blur" }
        ],
      },

      //
      editDialogVisible: false,
      //
      editForm: {},
      //
      setRoleDialogVisible:false,
      //
      userInfo:{},
      //
      rolesList:[],
      //id
      selectedRoleId:''
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      //Promise   aynsc await
      const { data: result } = await this.$http.get("users", {
        params: this.queryInfo
      });
      if (result.meta.status !== 200)
        return this.$message.error("");
      this.userList = result.data.users;
      this.total = result.data.total;
      //console.log(result);
    },
    //pagesize
    handleSizeChange(newSize) {
      //console.log(newSize);
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    //  
    handleCurrentChange(newPage) {
      //console.log(newPage);
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    //swicth Put
    async userStateChanged(userInfo) {
      //console.log(userInfo);
      const { data: result } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      );
      if (result.meta.status !== 200) {
        //switchchangstate 
        userInfo.mg_state = !userInfo.mg_state;
        return this.$message.error("");
      }
      this.$message.success("");
    },
    // resetFields() 
    addDialogClosed() {
      //console.log(this.$refs.addFormRef)
      this.$refs.addFormRef.resetFields();
    },
    //
    addUser() {
      // validtrue false 
      this.$refs.addFormRef.validate(async valid => {
        //console.log(valid)
        if (!valid) {
          return;
        }
        //
        const { data: result } = await this.$http.post("users", this.addForm);
        //console.log(result)
        if (result.meta.status !== 201) {
          return this.$message.error("");
        }
        this.$message.success("");
        //
        this.addDialogVisible = false;
        //
        this.getUserList();
      });
    },
    //
    async showEditDialog(id) {
      const { data: result } = await this.$http.get("users/" + id);
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.editForm = result.data;
      this.editDialogVisible = true;
      //console.log(id)
    },
    //  
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //)
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return;
        }
        //
        const { data: result } = await this.$http.put(
          "users/" + this.editForm.id,
          { email: this.editForm.email, mobile: this.editForm.mobile }
        );
        if (result.meta.status !== 200) {
          return this.$message.error("");
        }
        //
        this.editDialogVisible = false;
        this.$message.success("");
        //
        this.getUserList();
      });
    },
    //ID
    async removeUserById(id) {
      //
      const confirmresult = await this.$confirm(
        ",?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => {
        return err;
      });
      // confirm cancel
      if (confirmresult !== "confirm") {
        return this.$message.info("");
      }
      //
      const { data: result } = await this.$http.delete("users/" + id);
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getUserList();
    },
    //
    async setRole(userInfo){
      this.userInfo=userInfo
      //,
      const {data:result}=await this.$http.get('roles')
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.rolesList=result.data
      //
      this.setRoleDialogVisible=true
    },
    //
    async saveRoleInfo(){
      // return 
      if(!this.selectedRoleId){
        return this.$message.error('!')
      }
      const {data:result}=await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selectedRoleId})
      console.log(result)
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.setRoleDialogVisible=false;
      this.getUserList();
    },
    // 
    setRoleDialogClosed(){
      this.selectedRoleId=''
      this.userInfo={}
    }
  }
};
</script>

<style lang="less" scoped>
</style>