<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>

      <!--   children: 'children'  hasChildren-->
      <el-table
        :data="cateList"
        row-key="cat_id"
        border
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
      >
        <!-- el-table-columntype=index  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="cat_name" label=""></el-table-column>
        <el-table-column prop="cat_deleted" label="">
          <template v-slot:default="scope">
            <!-- <el-switch v-model="scope.row.cat_deleted"></el-switch> -->
            <!-- <i :class="'scope.row.cat_deleted' ? 'el-icon-success style=color:lightgreen' : 'el-icon-error'"></i>   -->
            <i
              class="el-icon-success"
              v-if="scope.row.cat_deleted === false"
              style="color:lightgreen;"
            ></i>
            <i class="el-icon-error" v-else style="color:red;"></i>
          </template>
        </el-table-column>
        <el-table-column prop="cat_level" label="">
          <template v-slot:default="scope">
            <el-tag size="mini" v-if="scope.row.cat_level == '0'"></el-tag>
            <el-tag size="mini" type="success" v-else-if="scope.row.cat_level == '1'"></el-tag>
            <el-tag size="mini" type="warning" v-else-if="scope.row.cat_level == '2'"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot:default="scope">
            <el-button type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.cat_id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeUserById(scope.row.cat_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[3, 5, 10, 20]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addCateDialogVisible" width="40%" @close="addCateDiaologClosed">
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- :options v-model js-->
          <el-cascader
            v-model="selectedKeys"
            :options="parentCateList"
            :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name' ,children:'children',checkStrictly:true}"
            @change="parentCateChanged"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>

      <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="40%">
      <el-form :model="editForm" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 10
      },
      //,
      cateList: [],
      //
      total: 0,
      //
      addCateDialogVisible: false,
      //
      addCateForm: {
        //
        cat_name: "",
        //id 
        cat_pid: 0,
        // 1
        cat_level: 0
      },
      //
      addCateFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      parentCateList: [],
      //ID
      selectedKeys:[],
      //
      editDialogVisible:false,
      //
      editForm:{}

    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    //
    async getCateList() {
      const { data: result } = await this.$http.get("categories", {
        params: this.querInfo
      });
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      //console.log(result.data);
      //,cateList
      this.cateList = result.data.result;
      //
      this.total = result.data.total;
    },
    //pagesize
    handleSizeChange(newSize) {
      this.querInfo.pagesize = newSize;
      this.getCateList();
    },
    //pagenum
    handleCurrentChange(newPage) {
      this.querInfo.pagenum = newPage;
      this.getCateList();
    },

    // 
    showAddCateDialog() {
      //
      this.getParentCateList();
      this.addCateDialogVisible = true;
    },

    //
    async getParentCateList() {
      const { data: result } = await this.$http.get("categories", {
        params: { type: 2 }
      });
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.parentCateList = result.data;
    },

    //
    parentCateChanged(){
      //:propsemitPath:false  value this.selectedKeys
      //console.log(this.selectedKeys)
      //selectedKeyslength0  
      if(this.selectedKeys.length > 0){
        //0 1 2 id id 21 10
        this.addCateForm.cat_pid=this.selectedKeys[this.selectedKeys.length - 1]
        //id id2
        this.addCateForm.cat_level=this.selectedKeys.length
        return 
      }else{
        //  
        this.addCateForm.cat_pid=0
        this.addCateForm.cat_level=0
      }
    },
    // 
    addCate(){
      //console.log(this.addCateForm)
      //   ref validate
      this.$refs.addCateFormRef.validate(async valid=>{
        if(!valid) return 
        //return  
        const {data:result}=await this.$http.post('categories',this.addCateForm)
        if(result.meta.status!==201){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    //,
    addCateDiaologClosed(){
      //addCateFormRefref refhtmlid dom  resetFields  
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },

    //
    async showEditDialog(id){
      //console.log(id)
      const {data:result} = await this.$http.get("categories/" + id)
      if(result.meta.status!==200){
        return this.$message.error('')
      }
      this.editForm = result.data;
      this.editDialogVisible=true
    },
    //
    async editUserInfo(){
      const {data:result} = await this.$http.put('categories/' + this.editForm.cat_id,{cat_name:this.editForm.cat_name})
      if(result.meta.status!==200){
        return this.$message.error('')
      }
      this.editDialogVisible=false 
      this.$message.success("");
      this.getCateList();
    },

    //ID
    async removeUserById(id){
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const {data:result}=await this.$http.delete('categories/'+ id)
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getCateList();
    }

  }
};
</script>

<style lang="less" scoped>
.el-cascader{
  width: 100%;
}
</style>