<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title=":" type="warning" :closable="false" show-icon></el-alert>

      <!--  span-->
      <el-row class="cat_opt">
        <el-col>
          <span>:</span>
          <!--  -->
          <el-cascader
            v-model="selectedCateKeys"
            :options="cateList"
            :props="{ expandTrigger: 'hover',value:'cat_id',label:'cat_name' ,children:'children' }"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addDialogVisible=true"
          ></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--   native inputnativeinput-->
            <el-table-column type="expand">
              <template v-slot:default="scope">
                <!-- tag -->
                <el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleClose(index,scope.row)">{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template v-slot:default="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeUserById(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addDialogVisible=true"
          ></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!-- type="expand"  -->
            <el-table-column type="expand">
              <template v-slot:default="scope">
                <!-- tag -->
                <el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleClose(index,scope.row)">{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template v-slot:default="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeUserById(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="addDialogVisible"
      width="40%"
      @close="addDialogVisibleClosed"
    >
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParamsto "> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="editDialogVisible"
      width="40%"
      @close="editDialogVisibleClosed"
    >
      <!--  -->
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams "> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      cateList: [],
      //
      selectedCateKeys: [],
      // ,
      activeName: "many",
      //
      manyTableData: [],
      //
      onlyTableData: [],
      //
      addDialogVisible: false,
      //
      addForm: {
        attr_name: ""
      },
      //
      addFormRules: {
        //
        attr_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      editDialogVisible: false,
      //
      editForm: {
        attr_name: ""
      },
      

    };
  },
  created() {
    this.getCateList();
  },

  methods: {
    //
    async getCateList() {
      const { data: result } = await this.$http.get("categories");
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.cateList = result.data;
      //console.log(this.cateList);
    },
    // 
    handleChange() {
      this.getParamsData();
    },
    //tab
    handleClick() {
      this.getParamsData();
      //console.log(this.activeName);
    },

    //
    async getParamsData() {
      //
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = [];
        //3 
        this.manyTableData=[];
        this.onlyTableData=[];
        return;
      }
      //
      //console.log(this.selectedCateKeys);
      //ID  
      const { data: result } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      );
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      //(item.attrVals || '').split(' ') 
      //foreach   true false 
      result.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : [];
        //  tag
        item.inputVisible=false
        //  
        item.inputValue=''
      });
      //console.log(result);
      if (this.activeName === "many") {
        this.manyTableData = result.data;
      } else {
        this.onlyTableData = result.data;
      }
    },
    //
    addDialogVisibleClosed() {
      //refs
      this.$refs.addFormRef.resetFields();
    },
    //  
    //  
    addParamsto() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return;
        }
        const { data: result } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
          }
        )

        if (result.meta.status !== 201) {
        return this.$message.error("");
        }
        this.$message.success("");
        this.addDialogVisible = false;
        this.getParamsData();

      });
      
    },

    // 
    async showEditDialog(attr_id) {
      const { data: result } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        {
          params: { attr_sel: this.activeName }
        }
      );
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.editForm = result.data;
      this.editDialogVisible = true;
    },
    //
    editDialogVisibleClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //   attr_idattrId
    editParams() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        const {
          data: result
        } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )

        if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.editDialogVisible = false;
      this.getParamsData();

      });
      
    },
    //ID
    async removeUserById(id) {
      //
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);

      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      //categories/:id/attributes/:attrid
      const { data: result } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${id}`
      );
      if (result.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getParamsData();
    },
    // enter
    async handleInputConfirm(row){
      if(row.inputValue.trim().length === 0){
        row.inputValue=''
        row.inputVisible = false
        return 
      }
      //return  
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue=''
      row.inputVisible = false
      // 
      this.saveAttrVals(row)
    },
    //attr_vals 
    async saveAttrVals(row){
      //,
      const {data:result}=await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name:row.attr_name,
        attr_sel:row.attr_sel,
        // join
        attr_vals:row.attr_vals.join(' ')
      })

      if(result.meta.status!==200){
        return this.$message.error("");
      }
      this.$message.success("");
    },

    // 
    showInput(row){
      row.inputVisible = true
      // $nextTick,,
      this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    //
    handleClose(i,row){
      row.attr_vals.splice(i,1)
      this.saveAttrVals(row)

    }
  },

  //computed
  computed: {
    //isBtnDisabled true
    // true false
    isBtnDisabled() {
      // return this.selectedCateKeys.length!==3
      if (this.selectedCateKeys.length !== 3) {
        return true;
      }
      return false;
    },

    //ID
    cateId() {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2];
      }
      return null;
    },
    //
    titleText() {
      if (this.activeName === "many") {
        return "";
      }
      return "";
    }
  }
};
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-cascader {
  margin: 10px;
}
.el-tag {
  margin: 5px;
}
.input-new-tag{
  width: 150px;
}
</style>